<template>
  <div class="Navbar">
    <div class="NavbarHeight">
      <!-- 桌面显示 -->
      <div class="NavbarContainer">
        <div class="Navitem-left">
          <li class="NavItem">
            <a href="#Intro" class="NavLink">{{ $t("home.introNav") }}</a>
          </li>
          <li class="NavItem">
            <a href="#Experience" class="NavLink">{{
              $t("home.ExperienceNav")
            }}</a>
          </li>
          <li class="NavItem">
            <a href="#ProjectCard" class="NavLink">{{
              $t("home.projectNav")
            }}</a>
          </li>
        </div>
        <div class="Navitem-right">
          <li class="NavItem">
            <a class="NavLink" @click="change()">中文/EN</a>
          </li>
          <li class="button">
            <a href="#ContactCard" class="NavButton">{{
              $t("home.contactNav")
            }}</a>
          </li>
        </div>
      </div>
      <!-- 响应图标 -->
      <div class="FoldMenu">
        <li
          class="MenuButton"
          :class="{ 'button-active': dropdown }"
          @click="toggleDropdown"
        >
          <a class="NavButton">{{ $t("home.menubarNav") }}</a>
          <i class="el-icon-caret-bottom"
            :class="{ 'rotate': dropdown }"
          ></i>
        </li>
      </div>
      <!-- 移动端显示 -->
      <div class="MobileNav" :class="{ show: dropdown }">
        <div class="MobileHeight">
          <div class="MobileNavWrapper">
            <li class="NavItem">
              <a href="#Intro" class="NavLink">{{ $t("home.introNav") }}</a>
            </li>
            <li class="NavItem">
              <a href="#Experience" class="NavLink">{{
                $t("home.ExperienceNav")
              }}</a>
            </li>
            <li class="NavItem">
              <a href="#ProjectCard" class="NavLink">{{
                $t("home.projectNav")
              }}</a>
            </li>
            <li class="NavItem">
              <a class="NavLink" @click="change()">中文/EN</a>
            </li>
            <li class="button">
              <a href="#ContactCard" class="NavButton">{{
                $t("home.contactNav")
              }}</a>
            </li>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Navbar",
  data() {
    return {
      dropdown: false,
      lang: "",
    };
  },

  created() {
    this.lang = navigator.language;
  },

  methods: {
    toggleDropdown() {
      this.dropdown = !this.dropdown;
    },
    change() {
      this.lang = this.lang === "zh" ? "en" : "zh";
      this.$i18n.locale = this.lang;
      if (this.lang == "en") {
        localStorage.setItem("lang", "en");
      } else {
        localStorage.setItem("lang", "zh");
      }
    },
  },
};
</script>

<style scoped>
.Navbar {
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  color: #344054;
}
.NavbarHeight {
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.NavbarContainer {
  max-width: 62.5rem;
  display: flex;
  justify-content: space-between;
  margin: auto;
  align-content: center;
}

.Navitem-left {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.Navitem-right {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.NavItem {
  list-style-type: none;
  margin-left: 1rem;
  margin-right: 1rem;
  font-size: 1.125rem;
  cursor: pointer;
}

.button {
  list-style-type: none;
  margin-left: 1rem;
  margin-right: 1rem;
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  padding-left: 1.5625rem;
  padding-right: 1.5625rem;
  background-color: #344054;
  color: #fff;
  border-radius: 0.625rem;
  cursor: pointer;
}

.MenuButton {
  list-style-type: none;
  margin-left: 1rem;
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  padding-left: 1.5625rem;
  padding-right: 1.5625rem;
  background-color: #344054;
  color: #fff;
  border-radius: 0.625rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.el-icon-caret-bottom{
  transition: all 400ms ease 0s;
}

.rotate{
  transform: rotate(180deg);
  transition: all 400ms ease 0s;
  color: #fff
}

.button:hover {
  background-color: #98a2b3;
  color: #1d2939;
  transition: all;
  transition-duration: 200ms;
}

.button-active {
  background-color: #98a2b3;
  color: #1d2939;
  transition: all;
  transition-duration: 200ms;
}

.NavItem:hover {
  opacity: 0.75;   
  text-decoration: underline;
  transition: all;
  transition-duration: 200ms;
}

.FoldMenu {
  display: none;
}

.MobileNav {
  height: 0px;
  overflow: hidden;
  border-radius: 15px;
  transition: height 400ms ease 0s;
}

.show {
  transition: height 400ms ease 0s;
  height: 17.5rem;
  display: block;
}

.MobileHeight {
  padding-top: 1.25rem;
}

.MobileNavWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.MobileNavWrapper > li {
  margin-top: 0.75rem;
  margin-bottom: 0.75rem;
}

.NavLink {
  text-decoration: none;
  color: #344054;
}

.NavButton {
  text-decoration: none;
  color: #fff;
}

@media screen and (max-width: 768px) {
  .NavbarContainer {
    display: none;
  }

  .FoldMenu {
    display: block;
    width: 8.4375rem;
  }
}
</style>
