import Vue from 'vue'
// 引入i18n插件
import VueI18n from 'vue-i18n'
// 引入语言包
import en from '@/lang/en/home'
import zh from '@/lang/zh/home'

Vue.use(VueI18n)
const messages = {
  en: {
    ...en,
  },
  zh: {
    ...zh,
  }
}
// 配置i18n
const i18n = new VueI18n({
  locale: localStorage.getItem('lang') || 'en', // 从缓存中获取当前的语言类型
  messages,
})
export default i18n
